<template>
    <div>
        <div v-if="isLoading" class="loader">
            <p class="loader__text">Loading...</p>
            <div class="loader__icon">
                <div class="ferry">
                    <div class="chimney"></div>
                    <div class="waves"></div>
                </div>
            </div>
        </div>
        <div class="page" v-else>
            <div class="page__body">
                <BuildStatuses :buildStatuses="buildBuildStatuses()" />
                <PullRequests :pullRequests="buildPullRequests()" />
            </div>
            <div class="page__footer">
                <Releases :releases="releases" />
            </div>
        </div>
    </div>
</template>

<script>
import Releases from './Releases';
import BuildStatuses from './BuildStatuses';
import PullRequests from './PullRequests';
import firebase from 'firebase';
import dateFormat from 'dateformat';

const firebaseConfig = {
    databaseURL: 'https://dp-developer-dashboard.firebaseio.com'
};

const app = firebase.initializeApp(firebaseConfig);
const db = app.database();
const pullRequestsRef = db.ref('pull_requests');
const releasesRef = db.ref('releases');
const buildStatusesRef = db.ref('build_statuses');

export default {
    name: 'Home',
    components: {
        Releases,
        BuildStatuses,
        PullRequests
    },
    data () {
        return {
            isLoading: true
        }
    },
    firebase: {
        pullRequests: {
            source: pullRequestsRef,
            readyCallback () {
                this.isLoading = false;
            }
        },
        releases: {
            source: releasesRef,
            readyCallback () {
                this.isLoading = false;
            },
            asObject: true
        },
        buildStatuses: {
            source: buildStatusesRef,
            readyCallback () {
                this.isLoading = false;
            }
        }
    },
    ready () {
        this.isLoading = false;
    },
    methods: {
        buildBuildStatuses () {
            const failedBuilds = [];
            const inProgressBuilds = [];
            const pausedBuilds = [];

            this.buildStatuses.forEach(repo => {
                repo['.value'].forEach(branch => {
                    if (branch.status === 'succeeded') {
                        return;
                    }

                    const build = {
                        repo: repo['.key'],
                        branch: branch.name,
                        key: `${repo['.key']}_${branch.name}`,
                        status: branch.status,
                        id: branch.id,
                        type: branch.type,
                        updated_on: dateFormat(branch.updated_on, "dd mmm yy (h:MM:ss tt)") || "not available"
                    }
                    if (branch.status === 'error' || branch.status === 'failed') {
                        failedBuilds.push(build);
                    }
                    if (branch.status === 'pending' || branch.status === 'started') {
                        inProgressBuilds.push(build);
                    }
                    if (branch.status === 'paused') {
                        pausedBuilds.push(build);
                    }
                });
            });

            return {
                failed: failedBuilds,
                in_progress: inProgressBuilds,
                paused: pausedBuilds
            };
        },
        buildPullRequests () {
            const pullRequests = [];
            this.pullRequests.forEach(repo => {
                try {
                    repo['.value'].forEach(pr => {
                        pullRequests.push({
                            id: pr['.key'],
                            type: 'Pull request',
                            name: pr.repo,
                            sub: pr.title,
                            date: dateFormat(pr.created_date, 'dd mmm yy (h:MM:ss tt)'),
                            user: { ...pr.user }
                        });
                    });
                } catch (error) {
                    console.error('Error trying to loop through pull requests for ' + repo);
                }
            });
            return pullRequests;
        },
        buildWarningItems () {
            // const pullRequests = this.pullRequests.map(pr => {
            //     return {
            //         id: pr['.key'],
            //         type: 'Pull request',
            //         name: pr.repo,
            //         sub: pr.title,
            //         date: pr.created_date,
            //         user: { ...pr.user }
            //     }
            // });

            const pullRequests = [];
            this.pullRequests.forEach(repo => {
                try {
                    repo['.value'].forEach(pr => {
                        pullRequests.push({
                            id: pr['.key'],
                            type: 'Pull request',
                            name: pr.repo,
                            sub: pr.title,
                            date: dateFormat(pr.created_date, 'dd mmm yy (h:MM:ss tt)'),
                            user: { ...pr.user }
                        });
                    });
                } catch (error) {
                    console.error('Error trying to loop through pull requests for ' + repo);
                }
            });

            return [ ...pullRequests ];
        }
    }
}
</script>

<style lang="scss">
body, html {
    font-size: 18px;
    -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
</style>


<style lang="scss" scoped>
.page {
    display: flex;
    flex-direction: column;
    height: 100vh;

    &__body {
        flex-grow: 1;
        overflow: scroll;
    }
}

.loader {
    position: relative;
    margin-top: 10rem;
    margin-left: 1rem;
    display: flex;
    justify-content: center;

    &__text {
        display: block;
        font-size: 2rem;
    }

    &__icon {
        display: block;
        position: absolute;
        bottom: -4rem;
        left: 48%;
    }
}

$waves: 'data:image/svg+xml;base64,';

$colors : (
accent: #f0cf3c,
top-layer: #ecf0f1,
bottom-layer: #2b2b2b
);

.ferry {
    position: absolute;
    transform: translateY(-50%);
    display: inline-block;
    width: 64px;
    height: 32px;
    border-radius: 3px 0 0 0;
    background-color: map-get($colors, bottom-layer);
    z-index: 1;
    animation: bop .48s ease-in-out alternate infinite;

    @keyframes bop {
        to {
            transform: translateY(-40%);
        }
    }

    box-shadow: inset 0 -5px 0 0 #e74c3c, inset 0 -15px 0 0 #2b2b2b, inset 0 -17px 0 0 map-get($colors, accent);
    border-top: 4px solid map-get($colors, top-layer); 

    &:after {
        content: "";
        position: absolute;
        top: -18px;
        right: 0;
        display: block;
        width: 45px;
        height: 15px;
        background-color: map-get($colors, top-layer);
        z-index: 1;
        border-radius: 3px 3px 0 0;
    }

    &:before {
        content: "";
        position: absolute;
        transform: rotate(-25deg);
        top: -5px;
        left: -3px;
        width: 10px;
        height: 50px;
        background-color: white;

    }

    .chimney {
        position: absolute;
        top: -35px;
        display: block;
        width: 17px;
        height: 22px;
        background-color: map-get($colors, accent);
        left: 32px;
        z-index: -1;
        transform: rotate(8deg);
        box-shadow: inset 0 3px 0 0 map-get($colors, bottom-layer);
    }

    .waves {
        position: absolute;
        bottom: -37px;
        display: block;
        width: 100px;
        height: 50px;
        background-image: url($waves);
        background-size: 133% 144%;
        animation: sail 4s linear infinite;

        @keyframes sail {
            to {
                background-position: 400px 0;
            }
        }
    }
}
</style>



